export type PlayerData = {
  discord_id: string;
  level: number;
  points: number;
  xp: number;
  role: string;
  unique_ability: string;
  wanted_level: number;
  inventory: string[];
};

export const INITIAL_PLAYER_DATA: PlayerData = {
  discord_id: "default",
  level: 1,
  points: 0,
  xp: 0,
  role: "Novice",
  unique_ability: "None",
  wanted_level: 0,
  inventory: []
};

export const PRESET_RESPONSES = {
  "!status": (player: PlayerData) => 
    `Status Report:\nLevel: ${player.level}\nXP: ${player.xp}\nPoints: ${player.points}\nRole: ${player.role}\nUnique Ability: ${player.unique_ability}\nWanted Level: ${player.wanted_level}`,
  
  "!inventory": (player: PlayerData) => 
    player.inventory.length > 0 
      ? `Inventory Contents:\n${player.inventory.join("\n")}`
      : "Your inventory is empty.",
  
  "!help": () => 
    "Available Commands:\n" +
    "!status - View your current stats\n" +
    "!inventory - Check your inventory\n" +
    "!explore - Explore the city\n" +
    "!mission - Get a new mission\n" +
    "!shop - Visit the black market\n" +
    "!help - Show this help message",

  "!explore": () => {
    const locations = [
      "Neon District",
      "Underground Markets",
      "Corporate Zone",
      "Tech Alley",
      "Red Light District"
    ];
    const events = [
      "You stumble upon a secret corporate data cache.",
      "A gang of cyber-enhanced thugs spots you.",
      "You find an abandoned cybernetics lab.",
      "A mysterious figure offers you a questionable deal.",
      "You discover a hidden entrance to the underground network."
    ];
    const location = locations[Math.floor(Math.random() * locations.length)];
    const event = events[Math.floor(Math.random() * events.length)];
    return `Location: ${location}\n${event}`;
  },

  "!mission": () => {
    const missions = [
      "Infiltrate the Arasaka Tower and extract sensitive data.",
      "Track down a rogue AI in the abandoned tech district.",
      "Protect a high-profile target during a corporate negotiation.",
      "Retrieve a stolen prototype from the black market.",
      "Investigate mysterious disappearances in the Neon District."
    ];
    return `New Mission Available:\n${missions[Math.floor(Math.random() * missions.length)]}`;
  },

  "!shop": () => {
    const items = [
      "Neural Enhancer - 500 credits",
      "Plasma Rifle - 1000 credits",
      "Stealth Cloak - 750 credits",
      "Cyber Deck Mk II - 1200 credits",
      "Med-Bot - 300 credits"
    ];
    return "Black Market Inventory:\n" + items.join("\n");
  }
};

export const SYSTEM_MESSAGES = {
  WELCOME: "Welcome to Sin City RPG! A world of neon-lit streets, corporate intrigue, and cyber-enhanced possibilities awaits. Type !help to see available commands.",
  LEVEL_UP: (level: number) => `🎉 Congratulations! You've reached level ${level}! New abilities and areas are now available.`,
  WANTED_INCREASE: (level: number) => `⚠️ Warning: Your wanted level has increased to ${level}. Corporate security forces are on high alert.`,
  ABILITY_UNLOCK: (ability: string) => `🔓 New ability unlocked: ${ability}! Type !abilities to learn more.`,
  ERROR: "System malfunction detected. Please try again or contact support if the issue persists."
};
