"use client";

import React, { useState, useEffect, useRef } from "react";
import { INITIAL_PLAYER_DATA, PRESET_RESPONSES, PlayerData, SYSTEM_MESSAGES } from "@/lib/game-data";

const GROQ_API_KEY = "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";

type Message = {
  role: string;
  content: string;
};

export default function HomePage() {
  const [messages, setMessages] = useState<Message[]>([
    { role: "system", content: SYSTEM_MESSAGES.WELCOME },
  ]);
  const [input, setInput] = useState("");
  const chatWindowRef = useRef<HTMLDivElement>(null);
  const [loading, setLoading] = useState(false);
  const [playerData, setPlayerData] = useState<PlayerData>(INITIAL_PLAYER_DATA);

  useEffect(() => {
    if (chatWindowRef.current) {
      chatWindowRef.current.scrollTop = chatWindowRef.current.scrollHeight;
    }
  }, [messages]);

  async function generateText(prompt: string, history: Message[]) {
    // Check for preset commands first
    if (prompt.startsWith("!")) {
      const command = prompt.toLowerCase();
      if (command in PRESET_RESPONSES) {
        const response = PRESET_RESPONSES[command as keyof typeof PRESET_RESPONSES];
        if (typeof response === "function") {
          return response(playerData);
        } else {
          return response;
        }
      }
    }

    // Otherwise, call GROQ API
    try {
      const response = await fetch("https://api.groq.com/openai/v1/chat/completions", {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${GROQ_API_KEY}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          model: "llama-3.1-8b-instant",
          messages: [...history, { role: "user", content: prompt }],
          max_tokens: 500,
          temperature: 0.3,
        }),
      });
      if (!response.ok) {
        throw new Error("Failed to fetch response from GROQ API");
      }
      const data = await response.json();
      return data.choices?.[0]?.message?.content || "No response";
    } catch (error) {
      console.error("Error generating text:", error);
      return SYSTEM_MESSAGES.ERROR;
    }
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!input.trim()) return;
    const userMessage = input.trim();
    appendMessage("user", userMessage);
    setInput("");
    setLoading(true);
    const assistantResponse = await generateText(userMessage, messages);
    appendMessage("assistant", assistantResponse);
    setLoading(false);
  }

  function appendMessage(role: string, content: string) {
    setMessages((prev) => [...prev, { role, content }]);
  }

  return (
    <div className="flex flex-col min-h-screen bg-[#0a0f1c] text-gray-200 font-['Fira_Mono']">
      <header className="bg-gray-800 p-6 flex justify-between items-center border-b-2 border-red-600 shadow-lg">
        <h1 className="text-3xl font-bold text-red-600 neon-text tracking-wider transform hover:scale-105 transition-transform">
          Sin City RPG
        </h1>
        <div className="bg-gray-900 px-4 py-2 rounded-lg border border-red-600/20">
          <span className="text-gray-400">Wanted Level: </span>
          <span className="text-red-500 font-bold">{playerData.wanted_level}</span>
          <div className="h-2 w-32 bg-gray-700 rounded-full mt-1">
            <div
              className="h-full bg-red-600 rounded-full transition-all duration-500"
              style={{ width: `${(playerData.wanted_level / 5) * 100}%` }}
            ></div>
          </div>
        </div>
      </header>
      <main className="flex flex-1 max-w-7xl mx-auto p-6 w-full gap-6">
        <div className="w-1/4">
          <div className="bg-gray-800/90 rounded-xl shadow-2xl p-6 backdrop-blur-sm border border-red-600/20">
            <h2 className="text-xl font-bold text-red-600 mb-4 border-b border-red-600/20 pb-2">
              Inventory
            </h2>
            <ul className="space-y-2">
              {playerData.inventory.length === 0 ? (
                <li className="text-gray-400 italic">No items yet.</li>
              ) : (
                playerData.inventory.map((item, index) => (
                  <li
                    key={index}
                    className="bg-gray-900/60 p-3 rounded-lg hover:bg-gray-900 transition-colors cursor-pointer flex items-center space-x-2"
                  >
                    <span className="w-2 h-2 bg-red-600 rounded-full"></span>
                    <span>{item}</span>
                  </li>
                ))
              )}
            </ul>
          </div>
        </div>
        <section className="flex-1 relative">
          <div className="absolute inset-0 bg-red-600 opacity-5 pattern-grid"></div>
          <div className="relative bg-gray-800/90 rounded-xl shadow-2xl p-8 backdrop-blur-sm border border-red-600/20">
            <div
              ref={chatWindowRef}
              className="flex flex-col chat-scroll h-[60vh] overflow-y-auto p-6 bg-gray-900/80 rounded-lg mb-6 space-y-4"
              aria-live="polite"
              aria-atomic="false"
            >
              {messages.map((msg, idx) => (
                <div
                  key={idx}
                  className={`max-w-xl break-words p-4 rounded-lg ${
                    msg.role === "user"
                      ? "message-user ml-auto"
                      : msg.role === "assistant"
                      ? "message-assistant mr-auto"
                      : "text-gray-400 italic text-center mx-auto bg-gray-800/50 px-6 py-3"
                  }`}
                >
                  {msg.content}
                </div>
              ))}
              {loading && (
                <div className="text-red-400 italic loading-dots self-start bg-gray-800/50 px-6 py-3 rounded-lg">
                  Generating response
                </div>
              )}
            </div>
            <form onSubmit={handleSubmit} className="flex space-x-4">
              <input
                type="text"
                aria-label="Message input"
                aria-required="true"
                autoComplete="off"
                placeholder="Type a command or message..."
                className="flex-grow rounded-lg bg-gray-700/90 text-gray-100 px-6 py-4 focus:outline-none input-glow border border-red-600/20"
                value={input}
                onChange={(e) => setInput(e.target.value)}
                disabled={loading}
              />
              <button
                type="submit"
                aria-label="Send message"
                className="bg-red-600 text-white rounded-lg px-8 py-4 font-semibold transition button-glow disabled:opacity-50 hover:bg-red-700"
                disabled={loading}
              >
                <i className="fas fa-paper-plane mr-2"></i>
                Send
              </button>
            </form>
          </div>
        </section>
      </main>
      <footer className="bg-gray-900/80 border-t border-red-600/20 p-4 text-center text-gray-500 text-sm backdrop-blur-sm">
        © 2024 Sin City RPG Bot. All rights reserved.
      </footer>
    </div>
  );
}
